<template>
    <div>
        <v-header>
            <div slot="right" :class="[(startVal!=0 && endVal!=0) || isLong ? 'h-right' : 'h-right opa-6']" v-if="type > 0">
                <a @click="setDate" class="mg-r10">确定</a></div>
        </v-header>

        <v-content :type="'choose'">
            <div class="section">
                <div class="matter" @click="splitSet">
                    <p class="title">特殊设置</p>
                    <div class="edit">
                        <p class="split">{{workSplit | week-format}}</p>
                        <span class="img transfer"></span>
                    </div>
                </div>
                <div class="matter" v-if="targetType==1">
                    <p class="title">长期有效</p>
                    <div class="control-box check-box">
                        <input type="checkbox" v-model="isLong" class="ctrl-box"/>
                        <span :class="['img',isLong ? 'checked' : 'none']"></span>
                    </div>
                </div>
            </div>

            <div :class="['date-map',{'opa-6':isLong}]">
                <div class="month clear-fix" v-for="item in months">
                    <div class="title">{{item.month}}月&nbsp;&nbsp;{{item.year}}</div>
                    <div class="dis-flex weekday">
                        <p v-for="weekday in weekdays"
                           :class="['flex-1',workSplit.indexOf(weekday) > -1 && workSplit.length < 13 ? 'active' : '']">
                            {{weekday | week-format}}</p>
                    </div>
                    <div class="days">
                        <ul style="height:{{item.daysLen*0.44}}rem" :class="'month'+item.month">
                            <li v-for="day in item.days"
                                :class="['day',day.day == 0 ? 'zero' : 'color-3',
                  day.value == startVal && day.value != 0 ? 'is-start' : '',
                  day.value == endVal && day.value != 0 ? 'is-end' : '',
                  item.lost!=0 && day.day < item.lost ? 'is-lost' : '',
                  startVal!=0 && endVal!=0 && (day.value < endVal && day.value > startVal) ? 'is-mid' : '']"
                                @click="chooseDay(item.year,item.month,day.day,$event)">
                                <p class="val">{{day.day == 0 ? '' : day.day}}</p>
                                <p class="mark" v-if="day.value == startVal && startVal != 0 && startVal != endVal">
                                    开始</p>
                                <p class="mark" v-if="day.value == endVal && endVal != 0 && startVal != endVal">结束</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </v-content>

    </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import $ from 'jquery'
  import {publish,order} from '../../const'
  import {zeroFill} from '../../filter'

  export default{
    route:{
      data({from,to}){
        if(this.isReady){
          return {
            state:!this.state
          }
        }
      }
    },
    ready(){
      this.loadQuery();
      this.isReady = true;
    },
    data(){
      return {
        isReady:false,
        state:false,
        //workDay: {startTime: '', endTime: '', split: '1,2,3,4,5,6,7'},
        type: 0,
        targetType:0,
        months: [],//{month: 1, days: [], lost: 0,year:2016,weekStart}
        startTime: {year: 0, month: 0, day: 0},
        endTime: {year: 0, month: 0, day: 0},
        workSplit: '1,2,3,4,5,6,7',
        startVal: 0,
        endVal: 0,
        weekdays: ['7', '1', '2', '3', '4', '5', '6'],
        isLong:false//是否长期
      }
    },
    methods: {
      loadQuery(){
        let query = this.$route.query;
        this.targetType = query.targetType;
        if (query.type && query.type > 0) {
          this.type = query.type;
          let p = this.targetType == 1?publish.get(this.type) : order.get(this.type);//区分发布还是协议
          if(!p){
            if (this.targetType==1){
              window.location.replace('#/publish');
            }else{
              window.location.replace('#/order');
            }
            return;
          }
          let workDay = p.workDay;
          let time;
          if (workDay.startTime && !workDay.timeless) {
            time = workDay.startTime.split(".");
            this.startTime = {year: parseInt(time[0]), month: parseInt(time[1]), day: parseInt(time[2])};
            this.startVal = this.calcTime(this.startTime);
          }
          if (workDay.endTime && !workDay.timeless) {
            time = workDay.endTime.split(".");
            this.endTime = {year: parseInt(time[0]), month: parseInt(time[1]), day: parseInt(time[2])};
            this.endVal = this.calcTime(this.endTime);
          }
          if (workDay.split) {
            this.workSplit = workDay.split;
          }
          if(this.targetType == 1){
            this.isLong = workDay.timeless;
          }
        }else{
          if (this.targetType==1){
            window.location.replace('#/publish');
          }else{
            window.location.replace('#/order');
          }
          return;
        }
        let d = new Date();
        let now = {year:d.getFullYear(),month:d.getMonth()+1,day:d.getDate()};
        let end = this.calcTime(this.endTime);
        if(end!=0 && end < this.calcTime(now)){//结束时间在本日之前 则清空结束时间
          this.startTime = {year: 0, month: 0, day: 0};
          this.endTime = {year: 0, month: 0, day: 0};
          this.startVal = 0;
          this.endVal = 0;
        }

        let start = this.calcTime(this.startTime);
        let d1 = {year:d.getFullYear(),month:d.getMonth()+1,day:1};
        let d2 = {year:d.getFullYear(),month:d.getMonth(),day:1};
        if(start!=0 && start<this.calcTime(d1) && start >= this.calcTime(d2)){//开始时间为上个月
          this.fullDateMap(d.getMonth()-1, 2);
        }else{
          this.fullDateMap(d.getMonth(), 1);
        }
      },
      chooseDay(y, m, d, event){
        if(this.isLong){
          return;
        }
        let day = $(event.currentTarget);
        console.log(`选择时间: ${y},${m},${d}`);
        if (day.is('.is-lost') && !day.is('.is-start') && !day.is('.is-end')) {
          return;
        }
        if (!this.startVal) {//开始位未标注
          this.startTime = {year: y, month: m, day: d};
          this.startVal = parseInt("" + y + zeroFill(m) + zeroFill(d));
          this.endTime = {year: y, month: m, day: d};
          this.endVal = parseInt("" + y + zeroFill(m) + zeroFill(d));
          return;
        } else {//开始时间已标注
          if (day.is('.is-start')) {//本时间为开始时间
            if (this.endVal&&this.endVal!=this.startVal) {//当结束时间已选时  设置结束时间为开始时间
              this.startTime = this.endTime;
              this.startVal = this.endVal;
            } else {//否则置空选择的时间
              if(this.endVal==this.startVal){
                this.endTime = {year: 0, month: 0, day: 0};
                this.endVal = 0;
              }
              this.startTime = {year: 0, month: 0, day: 0};
              this.startVal = 0;
            }
            return;
          } else if (day.is('.is-end')) {//本时间为结束时间 清空结束时间
            this.endTime = this.startTime;
            this.endVal = this.startVal;
          } else {//未选择的时间
            let start = this.startTime;
            //判断开始时间是否大于当前选择时间
            if (this.calcTime(this.startTime) > parseInt("" + y + zeroFill(m) + zeroFill(d))) {
              this.endTime = this.startTime;
              this.startTime = {year: y, month: m, day: d};
            } else {
              this.endTime = {year: y, month: m, day: d};
            }
            this.startVal = this.calcTime(this.startTime);
            this.endVal = this.calcTime(this.endTime);
            console.log(`时间值 ${this.startVal},${this.endVal}`);
          }
        }
      },
      calcTime(time){
        return parseInt("" + time.year + zeroFill(time.month) + zeroFill(time.day));
      },
      fullDateMap(m, n){//当月 填充的月份
        let date, lost = 0, days = [], month = 0;
        for (let i = m; i <= (m + n); i++) {
          date = new Date();
          if (parseInt(i) == date.getMonth()) {//计算当月已过去的时间
            lost = date.getDate();
          } else if(parseInt(i) < date.getMonth()){
            lost = new Date(date.getFullYear(), parseInt(i) + 1, 0).getDate() + 1;
          }else{
            lost = 0;
          }
          if (parseInt(i) == 12) {//下个月是1月
            date.setFullYear(date.getFullYear() + 1);
            date.setMonth(0);
          }else if(parseInt(i) == -1){//改月份为12月
            date.setFullYear(date.getFullYear() - 1);
            date.setMonth(11);
          } else {
            date.setMonth(i,1);
          }
          //格式后的date
          days = this.dayNumOfMon(date);
          month = date.getMonth() + 1;
          this.months.push({
            month: month,
            days: days,
            year: date.getFullYear(),
            lost: lost,
            daysLen: Math.ceil(days.length / 7)
          });
        }
      },
      dayNumOfMon(d){
        let days = [];
        let max = new Date(d.getFullYear(), (d.getMonth() + 1), 0).getDate();
        let noWeek = new Date(d.getFullYear(), d.getMonth(), 1).getDay();//获取该天是周几
        for (let i = 0; i < noWeek; i++) {//填充没有的时间
          days.push({day: 0, value: 0});
        }
        for (let i = 1; i <= max; i++) {
          days.push({day: i, value: parseInt('' + d.getFullYear() + zeroFill(d.getMonth() + 1) + zeroFill(i))});
        }
        return days;
      },
      setDate(){
        if (this.targetType != 1 && (this.startVal == 0 || this.endVal == 0)) {
          return;
        }
        if(!this.isLong && (this.startVal == 0 || this.endVal == 0)){
          return;
        }
        let query = this.$route.query;
        let p = this.targetType == 1?publish.get(this.type) : order.get(this.type);//区分发布还是协议
        let workDay = p.workDay;
        let start = this.startTime, end = this.endTime;
        workDay.startTime = start.year + '.' + zeroFill(start.month) + '.' + zeroFill(start.day);
        workDay.endTime = end.year + '.' + zeroFill(end.month) + '.' + zeroFill(end.day);

        //区分发布还是协议
        if (query.targetType==1){
          workDay.timeless = this.isLong;
          if(this.isLong){
            workDay.startTime = '';
            workDay.endTime = '';
          }
          p.workDay = workDay;
          publish.set(p, this.type);
        }else {
          p.workDay = workDay;
          order.set(p, this.type);
        }
        window.history.back();
      },
      splitSet(){
        let query = this.$route.query;
        if ((this.startVal == 0 || this.endVal == 0) && !this.isLong) {
          window.location.href = `#/choose/split?type=${query.type}&from=${query.from}&targetType=${query.targetType}`;
          return;
        }
        let p = query.targetType == 1?publish.get(this.type) : order.get(this.type);//区分发布还是协议
        let workDay = p.workDay;
        let start = this.startTime, end = this.endTime;
        workDay.startTime = start.year + '.' + zeroFill(start.month) + '.' + zeroFill(start.day);
        workDay.endTime = end.year + '.' + zeroFill(end.month) + '.' + zeroFill(end.day);

        //区分发布还是协议
        if (this.targetType==1){
            workDay.timeless = this.isLong;
          if(this.isLong){
            workDay.startTime = '';
            workDay.endTime = '';
          }
          p.workDay = workDay;
          publish.set(p, this.type);
        }else {
          p.workDay = workDay;
          order.set(p, this.type);
        }
        window.location.href = `#/choose/split?type=${query.type}&from=${query.from}&targetType=${query.targetType}`;
      }
    },
    watch:{
      'state':function(){
        this.type = 0;
        this.months = [];
        this.startTime = {year: 0, month: 0, day: 0};
        this.endTime = {year: 0, month: 0, day: 0};
        this.workSplit = '1,2,3,4,5,6,7';
        this.startVal = 0;
        this.endVal = 0;
        this.isLong = false;
        this.loadQuery();
      },
      'isLong':function(){
        if(this.isLong){
          this.startTime = {year: 0, month: 0, day: 0};
          this.endTime = {year: 0, month: 0, day: 0};
          this.startVal = 0;
          this.endVal = 0;
        }
      }
    },
    components: {VHeader, VContent}
  }

</script>
<style scoped>
  .section .edit {
    color: #333;
    float: right;
    margin-right: .3rem;
    width: auto;
  }

  .section .img.transfer {
    position: absolute;
    right: .1rem;
    top: .15rem;
    width: .08rem;
    height: .12rem;
    background-size: .08rem;
    background-image: url(../../assets/img/icon/arrow-right.png);
  }

  .date-map {
    background-color: #fff;
  }

  .date-map .title {
    line-height: .35rem;
    height: .35rem;
    background-color: #f4f4f4;
    font-size: .16rem;
    text-align: center;
  }

  .date-map .weekday {
    background-color: #e5e5e5;
    font-size: .12rem;
    text-align: center;
    height: .25rem;
    line-height: .25rem;
  }

  .weekday .active{
    background-color: #ccc;
  }

  .days .day {
    width: 14.28%;
    height: .44rem;
    background-color: #fff;
    font-size: .16rem;
    text-align: center;
    float: left;
  }

  .day .val {
    height: 50%;
    line-height: 2;
  }

  .day .mark {
    line-height: 2;
    font-size: .12rem;
  }

  .day.is-lost {
    color: #999;
  }

  .day.is-mid {
    background-color: #e0f9f5;
  }

  .day.is-start, .day.is-end {
    background-color: #00cda9;
    color: #fff;
  }

  .control-box{
    height: 100%;
    width: .4rem;
    float:right;
  }

  .control-box .img {
    position: absolute;
    left: .1rem;
    top: .15rem;
    width: .18rem;
    height: .18rem;
    background-size: .18rem;
  }

  .img.none {
    background-image: url(../../assets/img/icon/select03.png);
  }

  .img.checked {
    background-image: url(../../assets/img/icon/selected03.png);
  }

</style>



